<template>
  <div class="about full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view">
        <div class="about-content">
          <p>项目工场（PE Market）是清科集团投资银行品牌清科资本旗下的股权交易在线服务平台，以大数据和人工智能技术为驱动，采用“线上+线下”、“标准化+定制化”的融资服务，实现投资人与创业者的快速精准匹配。</p>
          <br>
          <p>项目工场为投资人节省筛选项目的时间成本，平台项目质量严格把控、信息细节翔实可信，不再百里挑一，而是一发而中。同时项目工场用最短的时间让创业者对接最合适的投资机构，使创业者将时间和精力真正放在公司的经营与项目的发展上。</p>
          <br>
          <p>我们致力于降低企业及投资人的资本运作门槛，以更高效的方式让企业引入新的投资，并帮助投资人寻找优质投资标的或实现股权退出。</p>
          <h3>精品项目抢先看</h3>
          <p>清科集团内部评审的精品项目单独在平台显示，提前为你筛选出专业投资人看好的项目。</p>
          <h3>广泛的资源平台</h3>
          <p>清科集团积累的所有投资机构资源，将通过项目工场开放给创业者，让创业者有机会在最大范围内更精准的寻找最匹配的投资人。</p>
          <h3>强大的专业团队</h3>
          <p>由具有三到十余年投行工作经验且独立完成交易的资深人员构成。</p>
          <h3>出色的成功效率</h3>
          <p>精准的智能匹配引擎辅以专业的人工筛选，实现投资人与创业者的最佳匹配。</p>
          <br>
        </div>
      </pem-scroll-view>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemScrollView
  },
  created () {
    this._initWXShare()
    this._setTitle('项目工场-关于我们')
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .about
    font-size: 0.24rem
    .about-content
      background-color: $color-panel
      padding: 0.28rem 0.38rem 0.76rem 0.38rem
      p, h3
        font-size: 0.28rem
        color: $color-text-body
        line-height: 0.38rem
        word-wrap: break-word
      h1, h3
        color: $color-text-title
      h3
        font-weight: bold
        margin-top: 0.38rem
      h1
        font-size: 0.32rem
        line-height: 0.44rem
        font-weight: bold
        text-align: center
        margin: 0.3rem 0
        i
          color: $color-text-caption
          font-weight: lighter

</style>



